import { IInputProps } from '../../../types/IInputProps';
import FormInput from '../FormInput/FormInput';

interface IFormProps {
  values: IInputProps[];
}

const Form = (props: IFormProps) => {

  return (
    <form>
      {props.values.map((input, index) => (
        <FormInput key={index} label={input.label} inputName={input.inputName} type={input.type} handleInputChange={input.handleInputChange}/>
      ))}
    </form>
  );
};

export default Form;
