import React, { useContext } from 'react';
import Card from '../../../components/card/Card/Card';
import { ReactComponent as Logo } from '../../../assets/svg/logo.svg';
import classes from './Login.module.scss';
import Form from '../../../components/form/Form/Form';
import { useState } from 'react';
import { ILoginData } from '../../../types/ILoginData';
import { IInputProps } from '../../../types/IInputProps';
import Button from '../../../components/Button/Button';
import { AppContext } from '../../../context/AppContext';
import { IdentityService } from '../../../services/identity-service';
import jwt_decode from 'jwt-decode';
import { IJwt } from '../../../types/IJwt';
import { IAppUser } from '../../../types/IAppUser';

const initialState: ILoginData = {
  email: '',
  password: '',
};

const Login = () => {
  const appState = useContext(AppContext);

  const [loginData, setLoginData] = useState(initialState);
  const [alertMessage, setAlertMessage] = useState('');

  const logInClicked = async (e: React.MouseEvent) => {
    e.preventDefault();
    if (loginData.email === '' || loginData.password === '') {
      setAlertMessage('Empty email or password!');
      return;
    }
    setAlertMessage('');
    const loginResponse = await IdentityService.Login('account/login', loginData);
    if (!loginResponse.ok) {
      setAlertMessage(loginResponse.messages![0]);
    } else {
      setAlertMessage('');
      const decodedJWT: IJwt = jwt_decode(loginResponse.data!.token);
      const appUserRole =
        decodedJWT['http://schemas.microsoft.com/ws/2008/06/identity/claims/role'];
      const appUserId =
        decodedJWT['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/nameidentifier'];

      const appUser: IAppUser = {
        id: appUserId,
        firstName: loginResponse.data!.firstName,
        lastName: loginResponse.data!.lastName,
        roles: [appUserRole],
        isLoggedIn: true,
      };
      appState.setAuthInfo(loginResponse.data!.token, appUser, null);
    }
  };

  const handleEmailChange = (
    target: HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement
  ) => {
    setLoginData({ ...loginData, email: target.value });
  };

  const handlePasswordChange = (
    target: HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement
  ) => {
    setLoginData({ ...loginData, password: target.value });
  };

  const loginInputs: IInputProps[] = [
    {
      label: 'Kasutajanimi',
      inputName: 'email',
      type: 'email',
      handleInputChange: (target) => handleEmailChange(target),
    },
    {
      label: 'Salasna',
      inputName: 'password',
      type: 'password',
      handleInputChange: (target) => handlePasswordChange(target),
    },
  ];

  return (
    <Card backgroundColor='light'>
      <div className={classes.login}>
        <div className={classes.header}>
          <Logo />
          {/* <SelectList items={languages} selectListName="languages" /> */}
        </div>
        {alertMessage && <span className={classes.error}>{alertMessage}</span>}
        <Form values={loginInputs} />
        <div className={classes.loginButtons}>
          <div className={classes.buttonRow}>
            <Button backgroundColor='dark' handleClick={logInClicked}>
              Logi sisse
            </Button>
          </div>
          <div className={classes.buttonRow}>
            <Button backgroundColor='light'>Registreeri</Button>
            <Button backgroundColor='light'>Unustasid salasna?</Button>
          </div>
        </div>
      </div>
    </Card>
  );
};

export default Login;
