import React from 'react';
import Container from '../Container/Container';

import { ReactComponent as DashboardSVG } from '../../../assets/svg/dashboard.svg';
import { ReactComponent as ProductsSVG } from '../../../assets/svg/products.svg';
import { ReactComponent as OrdersSVG } from '../../../assets/svg/checklist.svg';
import { ReactComponent as StockSVG } from '../../../assets/svg/warehouse.svg';
import { ReactComponent as RecipeSVG } from '../../../assets/svg/recipe-book.svg';
import { ReactComponent as SettingsSVG } from '../../../assets/svg/settings.svg';
import { ReactComponent as LogoutSVG } from '../../../assets/svg/log_out_button.svg';

import classes from './Header.module.scss';
import { useContext } from 'react';
import { AppContext, initialAppState } from '../../../context/AppContext';
import { Link } from 'react-router-dom';
import Dropdown from '../../../components/Dropdown/Dropdown';
import { IDropdownItem } from '../../../types/IDropdownItem';
import { useState } from 'react';
import { useEffect } from 'react';
import { BaseService } from '../../../services/base-service';
import { IAppUserDepartment } from '../../../dto/IAppUserDepartments';

const initialState: IDropdownItem[] = [];

const Header = () => {
  const appState = useContext(AppContext);

  const [departments, setDepartments] = useState(initialState);

  const loadDepartments = async () => {
    const result = await BaseService.getAllByUserId<IAppUserDepartment>(
      'AppUserDepartments/User',
      appState.appUser.id,
      appState.jwt!
    );

    if (result.ok) {
      const data: IAppUserDepartment[] = result.data!;
      const departments = data.map((item, index) => {
        const department = {
          id: item.department.id,
          name: item.department.name,
          selected: index === 0 ? true : false,
        };
        return department;
      });
      setDepartments(departments);
      const department = departments.find((item) => item.selected === true);
      appState.setAuthInfo(appState.jwt!, appState.appUser, {
        id: department!.id!,
        name: department!.name!,
      });
    }
  };

  useEffect(() => {
    loadDepartments();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleLogoutClick = () => {
    appState.setAuthInfo('', initialAppState.appUser, null);
  };

  const SERVICES = [
    {
      name: 'Tlaud',
      svg: <DashboardSVG />,
      href: '/',
    },
    {
      name: 'Tooted',
      svg: <ProductsSVG />,
      href: '/products',
    },
    {
      name: 'Tellimused',
      svg: <OrdersSVG />,
      href: '/orders',
    },
    {
      name: 'Ladu',
      svg: <StockSVG />,
      href: '/stock',
    },
    {
      name: 'Retseptid',
      svg: <RecipeSVG />,
      href: '/recipes',
    },
    {
      name: 'Seaded',
      svg: <SettingsSVG />,
      href: '/settings',
    },
  ];

  const handleSelectItem = (selectedItem: IDropdownItem) => {
    let tempList = [...departments];
    tempList.forEach((item) => {
      item.selected = false;
      if (item.id === selectedItem.id) {
        item.selected = true;
      }
    });
    setDepartments(tempList);
    appState.setAuthInfo(appState.jwt!, appState.appUser, {
      id: selectedItem.id,
      name: selectedItem.name,
    });
  };

  return (
    <header className={classes.header}>
      <Container>
        <div className={classes.container}>
          <div className={classes.services}>
            {SERVICES.map((service, index) => {
              return (
                <div key={index} className={classes.service}>
                  <Link to={service.href}>
                    <div>{service.svg}</div>
                  </Link>
                </div>
              );
            })}
          </div>
          <div className={classes.meta}>
            {appState.selectedDepartment && (
              <Dropdown
                items={departments}
                title={appState.selectedDepartment!.name}
                selectItem={handleSelectItem}
              />
            )}
            <span className={classes.name}>
              {appState.appUser?.firstName} {appState.appUser?.lastName}
            </span>
            <div className={classes.logout} onClick={handleLogoutClick}>
              <Link to='/'>
                <LogoutSVG />
              </Link>
            </div>
          </div>
        </div>
      </Container>
    </header>
  );
};

export default Header;
