import React from 'react';
import classes from './Layout.module.scss';

interface ILayoutProps {
  hasLightBackground: boolean;
  isContentAlignedCenter: boolean;
  children?: React.ReactNode;
}

const Layout = (props: ILayoutProps) => {

  const backgroundClass = props.hasLightBackground ? classes.light : classes.dark;
  const centerAlignment = props.isContentAlignedCenter ? classes.center : ""

  return (
    <div className={`${classes.layout} ${backgroundClass} ${centerAlignment}`}>
      {props.children}
    </div>
  )
}

export default Layout
