import { useState } from 'react';
import { IDropdownItem } from '../../types/IDropdownItem';
import classes from './Dropdown.module.scss';
import {ReactComponent as AngleUp } from '../../assets/svg/up-arrow-angle.svg';
import {ReactComponent as AngleDown } from '../../assets/svg/down-arrow.svg';

interface IDropdownProps {
  title: string,
  items: IDropdownItem[];
  selectItem: (item: IDropdownItem) => void;
}

const Dropdown = (props: IDropdownProps) => {
  const [isListOpen, setIsListOpen] = useState(false);
  const [headerTitle, setHeaderTitle] = useState(props.title);

  const toggleList = () => {
    setIsListOpen((prevState) => !prevState);
  };

  const handleSelectItem = (item: IDropdownItem) => {
    setHeaderTitle(item.name);
    toggleList();
    props.selectItem(item);
  }

  return (
    <div className={classes.wrapper}>
      <button type='button' className={classes.header} onClick={toggleList}>
        <div className={classes.headerTitle}>{headerTitle}</div>
        {isListOpen ? (
          <AngleUp />
        ) : (
          <AngleDown />
        )}
      </button>
      {isListOpen && (
        <div role="list" className={classes.list}>
          {props.items.map((item) => {
            if (item.name !== headerTitle) {
              return <button key={item.id} type="button" className={classes.listItem} onClick={() => handleSelectItem(item)}>{item.name}</button>
            }
            return null;
          })}
        </div>
      )}
    </div>
  );
};

export default Dropdown;
