import Layout from './containers/layout/Layout/Layout';
import { useState } from 'react';
import Header from './containers/layout/Header/Header';
import { Redirect, Route, Switch } from 'react-router-dom';
import Home from './containers/Home/Home';
import Login from './containers/identity/Login/Login';
import Products from './containers/products/Products';
import Page404 from './containers/Page404';
import { AppContextProvider, initialAppState } from './context/AppContext';
import { IAppUser } from './types/IAppUser';
import { IDepartment } from './dto/IDepartment';

function App() {
  const setAuthInfo = (
    jwt: string,
    appUser: IAppUser,
    selectedDepartment: IDepartment | null
  ): void => {
    setAppState({ ...appState, jwt, appUser, selectedDepartment });
  };

  const [appState, setAppState] = useState({ ...initialAppState, setAuthInfo });

  return (
    <AppContextProvider value={appState}>
      <Layout
        hasLightBackground={appState.appUser.isLoggedIn}
        isContentAlignedCenter={!appState.appUser.isLoggedIn}
      >
        {appState.appUser.isLoggedIn && <Header />}
        <Switch>
          <Route exact path='/' component={appState.appUser.isLoggedIn ? Home : Login} />
          {!appState.appUser.isLoggedIn && <Redirect to="/" />}
          <Route path='/products' component={Products} />
          <Route component={Page404} />
        </Switch>
      </Layout>
    </AppContextProvider>
  );
}

export default App;
