import Axios, { AxiosError, AxiosRequestConfig } from 'axios';
import { APIBaseUrl } from '../config';
import { IFetchResponse } from '../types/IFetchResponse';
import { IMessages } from '../types/IMessages';

export abstract class BaseService {
    protected static axios = Axios.create({
        baseURL: APIBaseUrl,
        headers: {
            'Content-Type': 'application/json'
        }
    });

    protected static getAxiosConfiguration(jwt?: string): AxiosRequestConfig | undefined {
        if (!jwt) return undefined;
        const config: AxiosRequestConfig = {
            headers: {
                Authorization: 'Bearer ' + jwt
            }
        };  
        return config;      
    }

    static async getAll<TEntity>(apiEndpoint: string, jwt?: string): Promise<IFetchResponse<TEntity[]>> {
        try {
            let response = await this.axios.get<TEntity[]>(apiEndpoint, BaseService.getAxiosConfiguration(jwt));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data
            };    
        }
        catch (err) {
            let error = err as AxiosError;
            return {
                ok: false,
                statusCode: error.response?.status ?? 500,
                messages: (error.response?.data as IMessages).messages,
            }
        }

    }

    static async getAllByUserId<TEntity>(apiEndpoint: string, userId: string, jwt?: string): Promise<IFetchResponse<TEntity[]>> {
      try {
          let response = await this.axios.get<TEntity[]>(`${apiEndpoint}/${userId}`, BaseService.getAxiosConfiguration(jwt));
          return {
              ok: response.status <= 299,
              statusCode: response.status,
              data: response.data
          };    
      }
      catch (err) {
          let error = err as AxiosError;
          return {
              ok: false,
              statusCode: error.response?.status ?? 500,
              messages: (error.response?.data as IMessages)?.messages,
          }
      }

  }

}